*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.picture-box-container{
    display: flex;
    flex-wrap: wrap;
    width: 96%;
    margin-left: 2%;
    margin-right: 2%;
    height: 18rem;
}

.picture-box-img{
    width: 50%;
    height: inherit;
}

@media (max-width:500px){
    .picture-box-container{
        display: block;
        height: fit-content;
        margin-bottom: -220px;
    }

    .picture-box-img{
        width: 90vw;
        margin-bottom: -5px;
        margin-left: 5vw;
        margin-right: 5vw;
    }
}